#navbar {
  overflow: hidden;
}

#navbar button {
  float: left;
  display: block;
  text-align: center;
  text-decoration: none;
}

.sticky {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
}

.sticky + .content {
  padding-top: 80px;
}

.container-hidden {
  opacity: 0;
  filter: blur(5px);
  transition: 0.3s all;
}

.container-show {
  opacity: 1;
  filter: blur(0);
  transition: 0.3s all;
}

.lcontainer-hidden {
  opacity: 0;
  filter: blur(5px);
  transform: translateX(-100%);
  transition: 0.3s all;
}

.lcontainer-show {
  opacity: 1;
  filter: blur(0);
  transition: 0.3s all;
  transform: translateX(0);
}

.rcontainer-hidden {
  opacity: 0;
  filter: blur(5px);
  transform: translateX(100%);
  transition: 0.3s all;
}

.rcontainer-show {
  opacity: 1;
  filter: blur(0);
  transition: 0.3s all;
  transform: translateX(0);
}
